import React from "react"
import { Card, Form, Input, Button, message, Tabs } from "antd"
import { JsonEditor as Editor } from "jsoneditor-react"
import "jsoneditor-react/es/editor.min.css"
import Terminal from "react-console-emulator"
import cbor from "cbor"

import {
  parseAuthenticatorData,
  parseAttestationObject,
  parseCredentials,
} from "./utils/output-parser"

import { derToPem } from "./utils/utils"

import base64url from "base64url"

import {
  getMakeCredentialsChallenge,
  sendWebAuthnResponse,
  getGetAssertionChallenge,
  verifyResponse,
  registerV2,
  loginV2,
} from "./webAuthAPI"
import {
  preformatMakeCredReq,
  publicKeyCredentialToJSON,
  preformatGetAssertReq,
  Base64Decode,
} from "./utils"

const { TabPane } = Tabs

const layout = {
  labelCol: { span: 8 },
  wrapperCol: { span: 16 },
}
const tailLayout = {
  wrapperCol: { offset: 8, span: 16 },
}

class App extends React.Component {
  state = {
    options: {},
    user: {},
    response: null,
    challenge: "",
    getResponse: null,
    clientData: {},
  }
  async componentDidMount() {
    const a = await window.PublicKeyCredential.isUserVerifyingPlatformAuthenticatorAvailable()
    if (!a) {
      message.error("Webauthnn not support")
    }

    window.parseAttestationObject = parseAttestationObject
    window.parseCredentials = parseCredentials
    window.parseAuthenticatorData = parseAuthenticatorData
    window.publicKeyCredentialToJSON = publicKeyCredentialToJSON
    window.base64url = base64url
    window.derToPem = derToPem
    window.dev = crypto
    window.buff = Buffer
  }

  createWebauthConfig(username, displayName) {
    const challenge = new Uint8Array(32)
    const userId = new Uint8Array(32)
    crypto.getRandomValues(challenge)
    crypto.getRandomValues(userId)

    const options = {
      publicKey: {
        challenge: challenge,

        rp: {
          name: "Webauthn Play",
          id: "webauthnplay.herokuapp.com",
        },

        user: {
          id: userId,
          name: username,
          displayName: displayName,
        },

        attestation: "direct",

        authenticatorSelection: { authenticatorAttachment: "platform" },
        pubKeyCredParams: [
          {
            type: "public-key",
            alg: -7, // "ES256" IANA COSE Algorithms registry
          },
        ],
      },
    }

    this.setState({
      challenge: challenge,
      user: { id: userId, name: username, displayName: displayName },
      options: options,
    })
    return options
  }

  createWebauthGetConfig() {
    const { challenge, user, response } = this.state
    if (response) {
      return {
        publicKey: {
          challenge: challenge,
          allowCredentials: [
            {
              type: "public-key",
              id: base64url.toBuffer(response.rawId),
              transports: ["usb", "ble", "nfc", "internal"],
            },
          ],
        },
      }
    }
  }

  handleRegister = async (values) => {
    const { username, name = "auto" } = values

    if (!username || !name) {
      alert("Name or username is missing!")
      return
    }
    this.setState({})
    const that = this

    const options = this.createWebauthConfig(username, name)

    try {
      const response = await navigator.credentials.create(options)

      const jsonBody = publicKeyCredentialToJSON(response)

      await registerV2({
        username,
        name,
        publicKeyCredential: jsonBody,
      })
      this.setState({
        response: jsonBody,
      })
    } catch (error) {
      message.error(error.message)
    }
  }

  handleLogin = async (values) => {
    let username = values.username2
    const { response, attestation } = this.state
    // if (!response) {
    //   message.error("Not registered")
    //   return
    // }

    if (!username) {
      message.error("Username is missing!")
      return
    }

    const options = this.createWebauthGetConfig()

    try {
      const response2 = await navigator.credentials.get(options)

      const jsonBody = publicKeyCredentialToJSON(response2)
      const res = await loginV2({ username, publicKeyCredential: jsonBody })
      this.setState({
        getResponse: publicKeyCredentialToJSON(response2),
      })

      if (res.result) {
        message.success("Verified")
      } else {
        message.error("Not Verified")
      }
    } catch (error) {
      message.error(error.message)
    }
  }
  render() {
    const { response, getResponse } = this.state
    return (
      <div className="card-container">
        <Tabs type="card">
          <TabPane tab="Register" key="1">
            <div
              style={{
                display: "flex",
                alignItems: "center",
                justifyContent: "center",
              }}
            >
              <Form
                onFinish={this.handleRegister}
                {...layout}
                id="form_register"
              >
                <Form.Item
                  label="User Name"
                  name="username"
                  rules={[
                    { required: true, message: "Please input your username!" },
                  ]}
                >
                  <Input></Input>
                </Form.Item>
                <Form.Item
                  label="Name"
                  name="name"
                  rules={[
                    { required: true, message: "Please input your name!" },
                  ]}
                >
                  <Input></Input>
                </Form.Item>
                <Form.Item {...tailLayout}>
                  <Button type="primary" htmlType="submit" form="form_register">
                    Register
                  </Button>
                </Form.Item>
              </Form>
            </div>
          </TabPane>
          <TabPane tab="Login" key="2">
            <div
              style={{
                display: "flex",
                alignItems: "center",
                justifyContent: "center",
              }}
            >
              <Form onFinish={this.handleLogin} id="form_update">
                <Form.Item
                  label="User Name"
                  name="username2"
                  rules={[
                    { required: true, message: "Please input your username!" },
                  ]}
                >
                  <Input></Input>
                </Form.Item>
                <Form.Item {...tailLayout}>
                  <Button type="primary" htmlType="submit" form="form_update">
                    Login
                  </Button>
                </Form.Item>
              </Form>
            </div>
          </TabPane>
        </Tabs>
        {response ? (
          <div>
            {" "}
            <p>Create Credential Response</p> <br></br>
            <Editor
              value={{
                raw: response,
              }}
            ></Editor>
          </div>
        ) : (
          <div />
        )}
        {getResponse ? (
          <div>
            <p>Get Credential Response</p> <br></br>
            <Editor
              value={{
                raw: getResponse,
              }}
            ></Editor>
          </div>
        ) : (
          <div></div>
        )}
      </div>
    )
  }
}
export default App
